	import  downWrap  from "../downWrap.vue";
	export default {
		components:{
			 downWrap
		},
		props:{
			 listData:{
                 type:Array,
				  default:()=>[1,2,3]
			 },
			 current:{
				 type:Object,
				 default:{
					 stage_code:0,
					 subject_code:"",
					 subject_name:""
				 }
			 }
		},
		data() {
			return {
				visable:false
			};
		},
		created(){
			
		},
		methods:{
			 show(){
				 this.visable = ! this.visable;
			 },
			 choice(index,code,name){
				 var obj = {
					 stage_code:index+1,
					 subject_code:code,
					 subject_name: name
				 }
				 this.show();
				 this.$emit('choice',obj)
			 }
		}
	}
